*{
  font-family: 'Oxygen', sans-serif;
}

body{
  height: 100%;
  width: 100%;
}
.app{  
  display: -ms-flexbox;  
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left:0;
  right: 0;
}

.demo-panel{
  -ms-flex:1 1;
      flex:1 1;
  overflow-x: auto;
  background-color: #f8f9fa;
}

.nav-panel{
  -ms-flex-negative: 0;
      flex-shrink: 0;
  width: 300px;
}

.demo-navigator{
  list-style: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  width: 300px;
  bottom: 0px;
  top: 0px;
  left: 0px;
  padding: 0px;
  margin: 0;
  -webkit-box-shadow: 1px 0px 5px #ccc;
          box-shadow: 1px 0px 5px #ccc;
  z-index: 1;  
}

.demo-navigator li{
  height: 50px;
  display: block;
}

.demo-header {
  height: 50px;
  background-color: #eee;
}

.source-code{  
  float: right;
  height: 60px;
  line-height: 60px;
  vertical-align: middle;
  cursor: pointer;
}

.source-code img{
  width: 30px;
  height: 30px;
  vertical-align: middle;
}

.source-code span{
  font-size: 14px;
  color: #eee;
  margin-left: 10px;
  vertical-align: middle;
}

.demo-navigator button {
  border: 1px solid #348;
  text-align: center;
  height: 100%;
  width : 100%;
  display: block;
  background-color: rgb(64, 105, 182);
  cursor: pointer;
  outline: 0;
  color: white;
  border-top: 1px solid rgb(116, 137, 223);
  border-bottom: 1px solid rgb(43, 76, 185);
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease;
  font-size: 16px;
}

.demo-navigator button:hover {
  background-color: rgb(47, 94, 182);
}


.simple-page{
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  margin-bottom: 50px;
}

.simple-page-scroller{
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  overflow-y: auto;
  height: 80vh;
}

.draggable-item{
  height: 50px;
  line-height: 50px;
  text-align: center;
  width : 100%;
  display: block;
  background-color: #fff;
  outline: 0;
  border: 1px solid rgba(0,0,0,.125);
  margin-bottom: 2px;
  margin-top: 2px;
}

.draggable-item-horizontal{
    height: 300px;
    padding: 10px;
    line-height: 100px;
    text-align: center;
    /* width : 200px; */
    display: block;
    background-color: #fff;
    outline: 0;
    border: 1px solid rgba(0,0,0,.125);
    margin-right: 4px;
}

.form-demo{
  width: 650px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  display: -ms-flexbox;
  display: flex
}

.form{
  -ms-flex: 3 1;
      flex: 3 1;
  /* width: 500px; */
  /* background-color: #f3f3f3; */
  border: 1px solid rgba(0,0,0,.125);
  border-radius: 6px;
  -webkit-box-shadow: 3px 3px 3px rgba(0,0,0,0.08), 0px 3px 3px rgba(0,0,0,0.08);
          box-shadow: 3px 3px 3px rgba(0,0,0,0.08), 0px 3px 3px rgba(0,0,0,0.08);
}

.form-fields-panel{
  -ms-flex: 1 1;
      flex: 1 1;
  margin-right: 50px;
}

.form-field{
  height: 50px;
  width: 250px;
  line-height: 30px;
  vertical-align: middle;
  padding: 10px;
  background-color: #fff;
  border: 1px solid #eee;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #ddd;
  /* background-color: #eee; */
  cursor: move;
  
}

.form-line {
  padding: 20px 30px;
  /* background-color: #f3f3f3; */
  border: 1px solid transparent;
  border-radius: 6px;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  -webkit-transition-property: border-color, background-color;
  -o-transition-property: border-color, background-color;
  transition-property: border-color, background-color;
  cursor: move;
}

.form-line.selected {
  /* border: 1px solid #ddd; */
  background-color: #f8f9fa;
  /* box-shadow: 0px 0px 10px 10px #ccc; */
}

.form-ghost{
  -webkit-transition: 0.18s ease;
  -o-transition: 0.18s ease;
  transition: 0.18s ease;
  -webkit-box-shadow: 1px 1px 5px 2px rgba(0,0,0,0.08);
          box-shadow: 1px 1px 5px 2px rgba(0,0,0,0.08);
}

.form-ghost-drop{
  -webkit-box-shadow: 0 0 2px 5px rgba(0,0,0,0.0);
          box-shadow: 0 0 2px 5px rgba(0,0,0,0.0);
}

.label {
  margin-bottom: 5px; 
}

.field {
  cursor: auto;
}

.field input, .field textarea, .field select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 3px;
  outline: none;
  -webkit-transition: border-color .3s ease;
  -o-transition: border-color .3s ease;
  transition: border-color .3s ease;
}

.field input[type="radio"], .field input[type="checkbox"] {
  width: auto;
}

.field input:focus, textarea:focus {
  border: 1px solid #80bdff;
  -webkit-box-shadow: 0 0 3px 0px #80bdff;
          box-shadow: 0 0 3px 0px #80bdff;
}

.field-group input, .field-group textarea, .field-group select{
  width: 49%;
  margin-right: 2%;
}

input:last-child{
  margin-right: 0;
}

.dragging {
  background-color: yellow;
}

.card-scene{
  padding: 50px;
  /* background-color: #fff; */
}

.card-container{
  width: 320px;
  padding: 10px;
  margin: 5px;
  margin-right: 45px;
  background-color: #f3f3f3;
  -webkit-box-shadow: 0 1px 1px rgba(0,0,0,0.12), 0 1px 1px rgba(0,0,0,0.24);
          box-shadow: 0 1px 1px rgba(0,0,0,0.12), 0 1px 1px rgba(0,0,0,0.24);
}

.card{
  margin: 5px;
  /* border: 1px solid #ccc; */
  background-color: white;
  -webkit-box-shadow: 0 1px 1px rgba(0,0,0,0.12), 0 1px 1px rgba(0,0,0,0.24);
          box-shadow: 0 1px 1px rgba(0,0,0,0.12), 0 1px 1px rgba(0,0,0,0.24);
  padding: 10px;
}

.card-column-header{
  font-size: 18px;
}

.column-drag-handle{
  cursor: move;
  padding: 5px; 
}

.card-ghost {
  -webkit-transition: -webkit-transform 0.18s ease;
  transition: -webkit-transform 0.18s ease;
  -o-transition: transform 0.18s ease;
  transition: transform 0.18s ease;
  transition: transform 0.18s ease, -webkit-transform 0.18s ease;
  -webkit-transform: rotateZ(5deg);
      -ms-transform: rotate(5deg);
          transform: rotateZ(5deg)
}

.card-ghost-drop{
  -webkit-transition: -webkit-transform 0.18s ease-in-out;
  transition: -webkit-transform 0.18s ease-in-out;
  -o-transition: transform 0.18s ease-in-out;
  transition: transform 0.18s ease-in-out;
  transition: transform 0.18s ease-in-out, -webkit-transform 0.18s ease-in-out;
  -webkit-transform: rotateZ(0deg);
      -ms-transform: rotate(0deg);
          transform: rotateZ(0deg)
}

.opacity-ghost {
  -webkit-transition: all .18s ease;
  -o-transition: all .18s ease;
  transition: all .18s ease;
  opacity: 0.8;
  /* transform: rotateZ(5deg); */
  background-color: cornflowerblue;
  -webkit-box-shadow: 3px 3px 10px 3px rgba(0,0,0,0.3);
          box-shadow: 3px 3px 10px 3px rgba(0,0,0,0.3);
}

.opacity-ghost-drop {
  opacity: 1;
  /* transform: rotateZ(0deg); */
  background-color: white;
  -webkit-box-shadow: 3px 3px 10px 3px rgba(0,0,0,0.0);
          box-shadow: 3px 3px 10px 3px rgba(0,0,0,0.0);
}


